.edit-form {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .edit-container {
    background-color: rgba(0, 0, 0, 0.1);
    padding: 20px;
    border-radius: 10px;
    transition: background-color 0.3s ease;
  }
  
  .edit-container:hover {
    background-color: rgba(0, 0, 0, 0.3);
  }
  
  h2 {
    text-align: center;
    margin-bottom: 10px;
  }
  
  
  .addBtn {
    transition: background-color 0.3s ease;
    cursor: pointer;
  }
  
  .addBtn:hover {
    background-color: #23527c;
  }
  