@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap");

body {
  background: #272727;
  font-family: "Montserrat", sans-serif;
  text-align:center;
  align:center;
}

.navMenu {
  border: none;
  border-radius: 0;
  width: 30%;
  align:center;
  font-size: 16px;
  text-align:center;
  display:flex;
  margin-left:30%;
}

.navMenu a {
  color: #f6f4e6;
  font-size: 1.2em;
  text-transform: uppercase;
  font-weight: 500;
  display: flex;
  margin:20px;
}

.navMenu a:hover {
  color: #fddb3a;
}

